*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: currentColor; }

ul,
ol,
li {
  list-style: none; }

html,
body {
  font-family: "Roboto", Helvetica, sans-serif;
  line-height: normal; }

@charset "UTF-8";
/*arrow-down*/
#one .down-btn, #two .content .down-btn {
  position: absolute;
  bottom: 20px;
  left: 50%;
  padding: 30px 50px 0;
  transform: translateX(-50%);
  text-align: center; }
  #one .down-btn .icon, #two .content .down-btn .icon {
    display: inline-block;
    font-size: 44px; }

/*更多信息按钮*/
#five .btn {
  display: inline-block;
  padding: 0 2.25em;
  line-height: 3em;
  height: 3em;
  background-color: rgba(255, 255, 255, 0.3);
  opacity: 0.25; }

#five .btn-disabled {
  cursor: not-allowed; }

/*section背景图*/
html,
body {
  height: 100%;
  color: #fff; }

.container {
  height: 100%; }
  .container > .item {
    height: 100%;
    background-color: #272833;
    background-image: linear-gradient(to top, rgba(23, 24, 32, 0.95), rgba(23, 24, 32, 0.95)); }

#one {
  position: relative;
  flex: 1; }
  #one .header {
    display: flex;
    justify-content: space-between;
    padding-left: 24px;
    height: 50px;
    line-height: 50px;
    font-size: 17px; }
    #one .header > .logo > a span {
      vertical-align: baseline; }
    #one .header > .logo > a sup {
      font-size: 14px;
      font-weight: 300; }
    #one .header .nav {
      position: absolute;
      top: 0px;
      right: 20px;
      font-size: 17px;
      font-weight: 100; }
      #one .header .nav .btn-default, #one .header .nav .btn-disabled {
        display: inline-block;
        padding: 0 15px;
        height: 30px;
        line-height: 30px; }
      #one .header .nav .btn-default {
        transition: color 0.2s ease; }
        #one .header .nav .btn-default:hover {
          color: #e44c65; }
      #one .header .nav .btn-disabled {
        cursor: not-allowed;
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 2px;
        opacity: 0.3; }
  #one .content {
    margin-top: 70px;
    margin-right: 105px;
    text-align: right; }
    #one .content > h2 {
      margin-bottom: 10px;
      font-size: 40px;
      font-weight: 300; }
    #one .content > p {
      line-height: 40px;
      font-size: 20px;
      font-weight: 300; }
    #one .content > .logo img {
      display: inline-block;
      border-radius: 50%; }

#two {
  position: relative;
  overflow: hidden; }
  #two .bg {
    width: 100%;
    background: url("../assets/img/pic02.jpg") no-repeat fixed center/cover; }
    #two .bg:before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-image: url("../assets/img/overlay.png"); }
  #two .content {
    position: absolute;
    bottom: 0;
    padding: 30px 60px 60px;
    border-top: 5px solid #e44c65;
    background-color: rgba(23, 24, 32, 0.95); }
    #two .content .item {
      flex: 1; }

#three {
  position: relative;
  overflow: hidden; }
  #three .bg {
    width: 100%;
    background: url("../assets/img/pic04.png") no-repeat fixed center/cover; }
    #three .bg:before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-image: url("../assets/img/overlay.png"); }
  #three .content {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 400px;
    background-color: rgba(23, 24, 32, 0.95);
    border-left: 5px solid #39c088; }

#four {
  position: relative;
  overflow: hidden; }
  #four .bg {
    width: 100%;
    background: url("../assets/img/pic03.gif") no-repeat fixed center/cover; }
    #four .bg:before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      background-image: url("../assets/img/overlay.png"); }
  #four .content {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 400px;
    background-color: rgba(23, 24, 32, 0.95);
    border-right: 5px solid #5480f1; }

#five {
  position: relative;
  padding: 69px 30px 38px;
  overflow: hidden;
  justify-content: center; }
  #five .title-wrapper {
    text-align: center;
    padding-bottom: 40px; }
    #five .title-wrapper .title {
      padding-bottom: 20px;
      font-size: 32px; }
    #five .title-wrapper .desc {
      display: inline-block;
      font-size: 20px; }
      #five .title-wrapper .desc:after {
        content: '';
        display: inline-block;
        position: relative;
        top: 30px;
        left: -12.5%;
        width: 125%;
        border-bottom: 3px solid #e44c65; }
  #five .label-wrapper {
    padding: 0 30px;
    text-align: center;
    font-weight: 300; }
    #five .label-wrapper .icon {
      display: inline-block;
      padding: 0.8em;
      font-size: 36px;
      background-color: #272833;
      border-radius: 50%; }
    #five .label-wrapper .title {
      padding: 20px 0 20px 0;
      font-size: 22px; }
    #five .label-wrapper .desc {
      line-height: 1.5;
      font-size: 15px;
      font-weight: 300;
      color: rgba(255, 255, 255, 0.75); }

#six {
  justify-content: center;
  padding: 69px 30px 38px;
  height: auto;
  background: #e44c65;
  font-weight: 300; }
  #six .title-wrapper {
    text-align: center;
    padding-bottom: 40px; }
    #six .title-wrapper .title {
      padding-bottom: 20px;
      font-size: 32px; }
    #six .title-wrapper .desc {
      display: inline-block;
      font-size: 20px; }
  #six .partner .logo-wrapper {
    padding-bottom: 20px;
    position: relative; }
    #six .partner .logo-wrapper img {
      width: 70%; }

#seven {
  justify-content: center;
  position: relative;
  padding: 60px 0 40px 0;
  height: auto;
  color: rgba(255, 255, 255, 0.5);
  background-image: none; }
  #seven .logo-wrapper {
    text-align: center; }
    #seven .logo-wrapper .icon {
      display: inline-block;
      font-size: 30px;
      margin-bottom: 15px; }
  #seven .copyright {
    justify-content: center;
    text-align: center; }
    #seven .copyright span {
      display: inline-block;
      padding: 0 10px;
      line-height: 12px;
      font-size: 12px;
      border-right: 1px solid; }
      #seven .copyright span:last-child {
        border: none; }
